<template>
    <div>
        <b>{{title}}</b>
        <div class="content_in_start"></div>
        <br>
        <div v-if="mode != 0">
            <table class="form">
            <tr>
                <td style="width:190px;"></td>
                <td><input type="text" v-model="dataObj.USER_OFFICE_NO" :readonly="mode != 2"></td>
                </tr>
                <tr>
                    <td></td>
                    <td>
                        <dataSelectBox v-model="dataObj.USER_TEAM_CODE" :items="teamCodeList" :input_id="'teamCode'" :viewText="' '" @input="val => {dataObj.USER_TEAM_CODE = val}" 
                            :setVal="dataObj.USER_TEAM_CODE" :disabled="mode != 2" style="width:400px"/> 
                    </td>
                </tr>
                <tr>
                    <td></td>
                    <td>
                        <dataSelectBox v-model="dataObj.USER_GROUP_CODE" :items="groupCodeList" :input_id="'groupCode'" :viewText="' '" @input="val => {dataObj.USER_GROUP_CODE = val}" 
                            :setVal="dataObj.USER_GROUP_CODE" :disabled="mode != 2" style="width:400px" /> 
                    </td>
                </tr>
                <tr>
                    <td></td>
                    <td>
                        <dataSelectBox v-model="dataObj.USER_POSITION_CODE" :items="posCodeList" :input_id="'posCode'" :viewText="' '" @input="val => {dataObj.USER_POSITION_CODE = val}" 
                            :setVal="dataObj.USER_POSITION_CODE" :disabled="mode != 2" style="width:400px" /> 
                    </td>
                </tr>
                <tr>
                    <td></td>
                    <td>
                        <dataSelectBox v-model="dataObj.USER_DUTY_CODE" :items="dutyCodeList" :input_id="'dutyCode'" :viewText="' '" @input="val => {dataObj.USER_DUTY_CODE = val}" 
                            :setVal="dataObj.USER_DUTY_CODE" :disabled="mode != 2" style="width:400px" /> 
                    </td>
                </tr>
                <tr>
                    <td>1</td>
                    <td><input type="text" v-model="dataObj.USER_CONFIRM1_ID" :readonly="mode != 2"></td>
                </tr>
                <tr>
                    <td>2</td>
                    <td><input type="text" v-model="dataObj.USER_CONFIRM2_ID" :readonly="mode != 2"></td>
                </tr>
            </table><br><br>
        </div>

        <span></span><br>
        <table class="form">
            <tr>
                <td style="width:190px;">ID</td>
                <td><input type="text" v-model="dataObj.USER_ID" :readonly="mode != 0"></td>
            </tr>
            <tr v-if="mode == 0 || mode == 1">
                <td></td>
                <td style="padding-left:7px"><input type="password" v-model="dataObj.USER_PWD" :readonly="mode == 1"></td>
            </tr>
            <tr v-if="mode == 1">
                <td> </td>
                <td style="padding-left:7px"><input type="password" v-model="chg_pwd"></td>
            </tr>
            <tr v-if="mode == 0 || mode == 1">
                <td></td>
                <td style="padding-left:7px"><input type="password" v-model="pwd_retype"></td>
            </tr>
            <tr>
                <td></td>
                <td><input type="text" v-model="dataObj.USER_NAME" :readonly="mode != 0 && mode != 1"></td>
            </tr>
            <tr>
                <td></td>
                <td><input type="text" v-model="dataObj.USER_CHINA_NAME" :readonly="mode != 0 && mode != 1"></td>
            </tr>
            <tr>
                <td></td>
                <td><input type="text" v-model="dataObj.USER_ENG_NAME" :readonly="mode != 0 && mode != 1"></td>
            </tr>
            <tr>
                <td></td>
                <td><input type="text" v-model="dataObj.USER_SECURITY_NO" :readonly="mode != 0 && mode != 1" placeholder="000000-0000000"></td>
            </tr>
            <tr>
                <td></td>
                <td><input type="text" v-model="dataObj.USER_AGE" :readonly="mode != 0 && mode != 1"></td>
            </tr>
            <tr>
                <td>H.P</td>
                <td><input type="text" v-model="dataObj.USER_HP" :readonly="mode != 0 && mode != 1" placeholder="000-0000-0000"></td>
            </tr>
            <tr>
                <td></td>
                <td><input type="date" v-model="dataObj.USER_BIRTH_DAY" :readonly="mode != 0 && mode != 1" placeholder="0000-00-00"></td>
            </tr>
            <tr>
                <td>E-MAIL</td>
                <td><input type="text" v-model="dataObj.USER_EMAIL" :readonly="mode != 0 && mode != 1"></td>
            </tr>
            <tr>
                <td></td>
                <td><input type="text" v-model="dataObj.USER_ADDRESS" :readonly="mode != 0 && mode != 1"></td>
            </tr>
            <tr>
                <td></td>
                <td><input type="text" v-model="dataObj.USER_HOME_ADDRESS" :readonly="mode != 0 && mode != 1"></td>
            </tr>
            <tr>
                <td></td>
                <td><input type="text" v-model="dataObj.USER_FAMILY_REGISTER" :readonly="mode != 0 && mode != 1"></td>
            </tr>
            <tr>
                <td></td>
                <td><input type="date" v-model="dataObj.USER_MARRY_DAY" :readonly="mode != 0 && mode != 1" placeholder="0000-00-00"></td>
            </tr>
            <tr>
                <td></td>
                <td><input type="text" v-model="dataObj.USER_EDU_GRADE" :readonly="mode != 0 && mode != 1"></td>
            </tr>
            <tr>
                <td></td>
                <td><input type="text" v-model="dataObj.USER_EDU_DIVISION" :readonly="mode != 0 && mode != 1"></td>
            </tr>
            <tr>
                <td></td>
                <td><input type="text" v-model="dataObj.USER_EDU_MAJOR" :readonly="mode != 0 && mode != 1"></td>
            </tr>
        </table>
        
        <br/><br /><span></span><br>
        <table class="form">
            <tr>
                <td style="width:190px;"></td>
                <td><input type="text" v-model="dataObj.USER_CAR_TYPE" :readonly="mode != 0 && mode != 1"></td>
            </tr>
            <tr>
                <td style="width:100px;"></td>
                <td><input type="text" v-model="dataObj.USER_CAR_MODEL" :readonly="mode != 0 && mode != 1"></td>
            </tr>
            <tr>
                <td></td>
                <td><input type="text" v-model="dataObj.USER_CAR_NO" :readonly="mode != 0 && mode != 1"></td>
            </tr>
            <tr>
                <td></td>
                <td><input type="text" v-model="dataObj.USER_CAR_COLOR" :readonly="mode != 0 && mode != 1"></td>
            </tr>
        </table>

        <div v-if="mode != 0">
            <br/><br /><span></span><br>
            <table class="form">
                <tr>
                    <td style="width:190px;"></td>
                    <td><input type="text" v-model="dataObj.USER_COMPANY" :readonly="mode != 2"></td>
                </tr>
                <tr>
                    <td></td>
                    <td><input type="date" v-model="dataObj.USER_JOIN_DAY" :readonly="mode != 2" placeholder="0000-00-00"></td>
                </tr>
                <tr>
                    <td></td>
                    <td><input type="date" v-model="dataObj.USER_RESIGN_DAY" :readonly="mode != 2" placeholder="0000-00-00"></td>
                </tr>
                <tr>
                    <td></td>
                    <td><input type="text" v-model="dataObj.USER_YEAR_COUNT" :readonly="mode != 2"></td>
                </tr>
                <tr>
                    <td> </td>
                    <td><input type="text" v-model="dataObj.USER_TOTAL_HOLYDAY" :readonly="mode != 2"></td>
                </tr>
                <tr>
                    <td> </td>
                    <td><input type="text" v-model="dataObj.USER_REMAIN_HOLYDAY" :readonly="mode != 2"></td>
                </tr>
            </table>

            <br/><br /><span></span><br>
            <table class="form">
                <tr>
                    <td style="width:190px;"> </td>
                    <td><input type="text" v-model="dataObj.USER_COMPANY_HOUSE_NAME" :readonly="mode != 2"></td>
                </tr>
                <tr>
                    <td> </td>
                    <td><input type="text" v-model="dataObj.USER_COMPANY_HOUSE" :readonly="mode != 2"></td>
                </tr>
                <tr>
                    <td> </td>
                    <td><input type="text" v-model="dataObj.USER_COMPANY_HOUSE_NO" :readonly="mode != 2"></td>
                </tr>
            </table>
        </div>
        <br><br>
        <div class="btn" @click="regConfirm" v-if="mode != 5">{{btnText}}</div>
        <div class="btn" @click="chgUpdate" v-if="mode == 5">{{btnText}}</div>
        <!-- div class="btn"></div -->
        <div class="btn" @click="$router.go(-1)"></div>

        <msgbox :title="boxObj.title" :message="boxObj.msg" :boxType="boxObj.bType" v-if="showMsgBox != null && showMsgBox" @close="showMessagebox" @callback="procRegister" />
    </div>
</template>

<script>
    import dataSelectBox from '@/components/dataSelectBox';
    import msgbox from  '@/components/messageBox';

    export default {
        name: 'userDetail',
        components: {
            dataSelectBox, msgbox
        },
        data() {
            return {
                mode: null, // 0: , 1:  , 2:  , 3:  , 4: , 5:  
                title: null,
                btnText: null,
                userId: null,
                chg_pwd: '',
                pwd_retype: null,
                teamCodeList: [],
                groupCodeList: [],
                posCodeList: [],
                dutyCodeList: [],
                dataObj: new Object(),
                boxObj: {
                    title: null,
                    msg: null,
                    bType: null
                },
                isShow: false
            };
        }, created() {
            this.mode = this.$route.params.mode;

            if (this.mode == 2 ) {
                this.title = "  ";
                this.btnText = "";
            } else if (this.mode == 0) {
                this.title = "";
                this.btnText = "";

                this.dataObj.USER_RESIGN_STATUS = 0; //0. 1. 2.
                // dummy  
                this.dataObj.USER_TEAM_CODE = "";
                this.dataObj.USER_GROUP_CODE = "";
                this.dataObj.USER_POSITION_CODE = "";
                this.dataObj.USER_DUTY_CODE = "";
                this.dataObj.USER_OFFICE_NO = "";
                this.dataObj.USER_AGE = 0;
                this.dataObj.USER_REMAIN_HOLYDAY = "";
                this.dataObj.USER_TOTAL_HOLYDAY = "";
            } else if (this.mode == 5) {
                this.title = " ";
                this.btnText = "";
            } else if (this.mode == 1) {
                this.title = "  ";
                this.btnText = " ";
            } else {
                this.title = " ";
                this.btnText = "";
            }

            if (this.mode != 0) {
                this.userId = this.$route.params.userId;
                this.getCombobox();
                this.getUserDetail();
            }    
        }, 
        computed: {
            showMsgBox: function() {
                return this.isShow;
            }
        },
        methods: {
            getUserDetail: function() {
                this.$store.dispatch('postApiCall', {url: '/api/proc/user-info-detail', userId: this.userId}).then(data => {
                    this.dataObj = data[0];
                    this.dataObj.USER_BIRTH_DAY = this.commonUtil.getDateFormat(data[0].USER_BIRTH_DAY);
                    this.dataObj.USER_JOIN_DAY = this.commonUtil.getDateFormat(data[0].USER_JOIN_DAY);
                    this.dataObj.USER_RESIGN_DAY = this.commonUtil.getDateFormat(data[0].USER_RESIGN_DAY);
                    this.dataObj.USER_MARRY_DAY = this.commonUtil.getDateFormat(data[0].USER_MARRY_DAY);
                });
            }, 
            regConfirm: function() {
                if (this.mode == 0) {
                    if (this.dataObj.USER_PWD != this.pwd_retype) {
                        this.showMessagebox(true, 'alert', ' ', '   .');
                        return false;
                    }
                } else if (this.mode == 1) {
                    if (this.dataObj.USER_PWD == this.chg_pwd) {
                        this.showMessagebox(true, 'alert', ' ', '    .');
                        return false;
                    }
                    if (this.chg_pwd.length > 0 && this.chg_pwd != this.pwd_retype) {
                        this.showMessagebox(true, 'alert', ' ', '     .');
                        return false;
                    }
                }

                this.dataObj.USER_HP = this.commonUtil.addHypenPhone(this.dataObj.USER_HP);
                this.dataObj.USER_SECURITY_NO = this.commonUtil.addHypenJumin(this.dataObj.USER_SECURITY_NO);
                var str = '  ?';
                if (this.mode == 0) {
                    str = ' ?';
                } else if (this.mode == 1) {
                    str = '  ?';
                }
                this.showMessagebox(true, 'confirm', ' ', str);
            },
            procRegister:function() {
                this.dataObj.USER_FROM = this.mode;
                var url = "/api/proc/join-confirm";

                this.$http.post(url, this.dataObj).then(res => {
                    if (!res.data.isSuccess) {
						this.showMessagebox(true, 'alert', '  ', data.message);
                        return;
					} else {
						this.showMessagebox(true, 'alert', ' ', '  .');
                        setTimeout(() => this.$router.go(-1), 1500);
					}
                });
            }, chgUpdate: function() {
                this.$router.push({name: 'userDetailView', params: {mode:1, userId: this.$store.getters.getId}});
                //this.mode = 1;
            },
            getCombobox: function() {
                this.commonUtil.getTeamCode().then(data => {
                    this.teamCodeList = data;
                });
                this.groupCodeList = this.commonUtil.getGroupCode();
                this.posCodeList = this.commonUtil.getPositionCode();
                this.dutyCodeList = this.commonUtil.getDutyCode();
            },
            showMessagebox: function(val, mode, title, msg) {                
				this.boxObj.bType = mode;
				this.boxObj.title = title;
				this.boxObj.msg = msg;
				this.isShow = val;
            }
        }
    }
</script>