<template>
    <div>
        <b> </b>
        <div class="content_in_start"></div>
        <input type="date" placeholder="" v-model="startDate" > ~
        <input type="date" placeholder="" v-model="endDate" > <br />
        <dataSelectBox v-model="placeCd" :items="placeList" :input_id="'placeCode'" @input="val => {placeCd = val}" :viewText="' '" /> 
        <dataSelectBox v-model="partCd" :items="partList" :input_id="'partCode'" @input="val => {partCd = val}" :viewText="' '" 
            :disabled="partList.length < 1" :setVal="partCd" /> 
        <dataSelectBox v-model="complexCd" :items="complexList" :input_id="'complexCode'" @input="val => {complexCd = val}" :viewText="' '" 
            :disabled="complexList.length < 1" :setVal="complexCd" /> 
        <dataSelectBox v-model="lineCd" :items="lineList" :input_id="'lineCode'" @input="val => {lineCd = val}" :viewText="' '" 
            :disabled="lineList.length < 1" :setVal="lineCd" /> 
        <dataSelectBox v-model="eqpSn" :items="eqpSnList" :input_id="'eqpSn'" @input="val => {eqpSn = val}" :viewText="' '" 
            :disabled="eqpSnList.length < 1" :setVal="eqpSn" style="width:160px" /> 
        <div class="search_btn" @click="search"></div>
        <br />
        <div v-if="checkList.length > 0">
            <div class="work_btn" @click="downFileCsv">CSV </div>
            <table>
                <thead>
                    <tr>
                        <th style="width:90px"></th>
                        <th style="width:90px"></th>
                        <th style="width:90px"></th>
                        <th v-for="(item, idx) in checkHeader" v-bind:key="idx" style="width:90px;max-width:130px;font-size:12px;text-align:center;line-height:20px"><pre>{{item}}</pre></th>
                        <th>  </th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item, idx) in checkList" v-bind:key="idx">
                        <td>{{item.CHECK_DAY}}</td>
                        <td>{{item.CHECK_TIME}}</td>
                        <td>{{item.USER_NAME}}</td>
                        <td v-for="(sub, idx1) in item.checkSheets" v-bind:key="idx1">{{sub}}</td>
                        <td>{{item.COMMENT}}</td>
                        <td>{{item.ETC}}</td>
                    </tr>
                </tbody>
            </table>
            <paging-navi v-if="totSize !== null && totSize > pageSize" :tot-size="chgTotal" :div-size="pageSize" :curr-page="currPage" @mvPage="mPg => {currPage = mPg}" />            
        </div>
        <div v-else>
            <br />
            <span>  .</span>
        </div>
        <msgbox :title="boxObj.title" :message="boxObj.msg" :boxType="boxObj.bType" v-if="showMsgBox != null && showMsgBox" @close="showMessagebox" />
        <outCsv :propsObj="doCSV" v-if="csvObj != null" @finish="val => {csvObj = val}" @fail="csvFail" />
    </div>
</template>
<script>
    import dataSelectBox from '@/components/dataSelectBox';
    import pagingNavi from '@/components/pagingNavi';
    import msgbox from  '@/components/messageBox';
    import outCsv from '@/components/outputCSV';
    const pageSize = 30;
    export default{
        components: {
            dataSelectBox, pagingNavi, msgbox, outCsv
        },
        data() {
            return {
                pageSize: pageSize,
                currPage : 1,
                totSize: null,
                startDate: null,
                endDate: null,
                placeCd: null,
                partCd: null,
                complexCd: null,
                lineCd: null,
                eqpSn: null,
                placeList: [],
                partList: [],
                complexList: [],
                lineList: [],
                eqpSnList: [],
                checkList: [],
                checkHeader: [],
                boxObj: {
                    title: null,
                    msg: null,
                    bType: null
                },
                isShow: false,
                csvObj: null
            };
        },
        created() {
            this.$emit('setMenu', 12); //  
            this.commonUtil.getPlaceList().then(data => {
                this.placeList = data;
            });
            this.endDate = this.commonUtil.getToday();

            //   7 
            var beforeday = new Date();
            beforeday.setDate(beforeday.getDate() - 30);
            this.startDate = this.commonUtil.getDateStr(beforeday);

            //   
            this.setSheetHeader();

        },
        computed: {
            chgTotal: function() {
                return this.totSize;
            },
            showMsgBox: function() {
                return this.isShow;
            },
            doCSV: function() {
                return this.csvObj;
            }
        },
        watch: {
            placeCd: function(newval) {
                this.clearValue('place');
                this.placeCd = newval;
                if (newval == null || newval.length < 1) {
                    return false;
                } 
                this.commonUtil.getPartList().then(data => {
                    this.partList = data;
                });
            },
            partCd: function(newval, oldval) {
                this.clearValue('part');
                this.partCd = newval;
                if (newval == null || newval.length < 1) {
                    return false;
                } 
                this.commonUtil.getComplexList().then(data => {
                    this.complexList = data;
                });
            },
            complexCd: function(newval) {
                this.clearValue('complex');
                this.complexCd = newval;
                if (newval == null || newval.length < 1) {
                    return false;
                } 
                this.commonUtil.getLineList().then(data => {
                    this.lineList = data;
                });
            }, lineCd: function(newval) {
                this.clearValue('line');
                this.lineCd = newval;
                if (newval == null || newval.length < 1) {
                    return false;
                } 
                this.eqpSnList = this.commonUtil.getEqpSnList();
            }
        }, methods: {
            setSheetHeader: function() {
                this.checkHeader.push("Pressure,\nTemp");
                this.checkHeader.push("Manifold,\nChamber");
                this.checkHeader.push("Quench,\nDrain H\nWet ");
                this.checkHeader.push("Tank,\n");
                this.checkHeader.push("Drain \n(, )");
                this.checkHeader.push(" .\n2S ");
                this.checkHeader.push(" \n(Power Vaccine)\n ");
                this.checkHeader.push(" Cable\n");
            },
            search: function() {
                if (this.eqpSn == null || this.eqpSn.length < 1) {
                    this.showMessagebox(true, 'alert', ' ', '  .');
                    return false;
                }
                this.getTotal();
                this.getList();
            },
            getList: function() {
                // query between    +
                var tempday = new Date(this.endDate);
                tempday.setDate(tempday.getDate() + 1);
                var eDate = this.commonUtil.getDateStr(tempday);

                var url = "/api/eqp-check-hist/" + this.startDate + "/" + eDate + "/" + this.eqpSn;
                var query = "?pageNo=" + this.currPage + "&pageSize=" + pageSize;
                url += query;
                this.$store.dispatch('getApiCall', url).then(data => {
                    var tempList = [];
                    var subList;

                    data.forEach(elem => {
                        subList = new Array();
                        elem.CHECK_DAY = this.commonUtil.getDateFormat(elem.CHECK_DATE);
                        elem.CHECK_TIME = this.commonUtil.getTimeFormat(elem.CHECK_DATE);

                        var keyList = Object.getOwnPropertyNames(elem);
                        
                        keyList.forEach(keyStr => {
                            if (keyStr.indexOf('CHK') > -1) {
                                subList.push(elem[keyStr]);
                            }
                        });
                        elem.checkSheets = subList;
                        tempList.push(elem);
                    });
                    this.checkList = tempList;
                });
            },
            getTotal: function() {
                // query between    +
                var tempday = new Date(this.endDate);
                tempday.setDate(tempday.getDate() + 1);
                var eDate = this.commonUtil.getDateStr(tempday);

                var url = "/api/eqp-check-hist/total-size/" + this.startDate + "/" + eDate + "/" + this.eqpSn;
                this.$store.dispatch('getApiCall', url).then(data => {
                   this.totSize = data[0].TOTAL_SIZE;
                });
            },
            clearValue: function(step) {
                this.eqpSn = '';
                this.eqpSnList = [];
                if (step == 'line') {
                    return false;
                }

                this.lineCd = '';
                this.lineList = [];
                if (step == 'complex') {
                    return false;
                }

                this.complexCd = '';
                this.complexList = [];
                if (step == 'part') {
                    return false;
                }

                this.partCd = '';
                this.partList = [];
                if (step == 'place') {
                    return false;
                }

                this.placeCd = '';
            }, showMessagebox: function(val, mode, title, msg) {                
				this.boxObj.bType = mode;
				this.boxObj.title = title;
				this.boxObj.msg = msg;
				this.isShow = val;
            }, downFileCsv: function() {
                var tempObj = new Object();
                tempObj.mode = 'CHECK_HIST'
                tempObj.condition = new Object();
                tempObj.condition.sDate = this.startDate;
                tempObj.condition.eDate = this.endDate;
                tempObj.condition.eqpSn = this.eqpSn;

                this.csvObj = tempObj;
            },
            csvFail: function(msg) {
                this.showMessagebox(true, 'alert', '  ', msg);
            }
        }
    }
</script>