<template>
    <div>
        <b> </b>
        <div class="content_in_start"></div>
        <dataSelectBox v-model="workTable" :items="chooseList" :input_id="'workTable'" @input="val => {workTable = val}" :viewText="' '" :setVal="workTable" /> 
        <br />
        <table>
            <thead>
                <tr>
                    <th></th>
                    <th></th>
                    <th style="width:calc(40%)"></th>
                    <th></th>
                </tr>
            </thead>
        </table>
    </div>
</template>

<script>
    const pageSize = 10;

    import dataSelectBox from '@/components/dataSelectBox';
    export default{
        components: {
            dataSelectBox
        },
        data() {
            return {
                workTable: null,
                codeList: [],
                viewList: [],
                chooseList: [
                    {title: '', val: 'WORK_PLACE_CODE_TBL'},
                    {title: '', val: 'WORK_PART_CODE_TBL'},
                    {title: '', val: 'WORK_COMPLEX_CODE_TBL'},
                    {title: '', val: 'WORK_SITE_CODE_TBL'},
                    {title: '', val: 'WORK_LINE_CODE_TBL'},
                    {title: '', val: 'WORK_FLOOR_CODE_TBL'},
                    {title: 'BAY', val: 'WORK_BAY_CODE_TBL'},
                    {title: '', val: 'USER_POSITION_CODE_TBL'},
                    {title: '', val: 'USER_DUTY_CODE_TBL'},
                    {title: 'EMS', val: 'EQP_LINE_EMS_CODE_TBL'},
                    {title: '', val: 'EQP_PROCESS_CODE_TBL'},
                    {title: '', val: 'EQP_SUB_PROC_CODE_TBL'},
                    {title: 'SCRUBBER CODE', val: 'EQP_SCR_CODE_TBL'},
                    {title: 'SCRUBBER MODEL CODE', val: 'EQP_SCR_MODEL_CODE_TBL'},
                    {title: 'EQP TYPE CODE', val: 'EQP_TYPE1_CODE_TBL'},
                    {title: 'EQP TYPE2 CODE', val: 'EQP_TYPE2_CODE_TBL'},
                    {title: '', val: 'EQP_GAS_INFO_TBL'},
                    {title: '', val: 'EQP_EXHAUST_CODE_TBL'},
                    {title: '', val: 'EQP_WASTE_INFO_TBL'},
                    {title: '  ', val: 'EQP_MAIN_MAKER_CODE_TBL'},
                    {title: '  ', val: 'EQP_MAIN_MODEL_CODE_TBL'},
                    {title: 'SCRUBBER  ', val: 'EQP_SCR_MAKER_CODE_TBL'},
                    {title: 'SCRUBBER   ', val: 'EQP_MGR_MAKER_CODE_TBL'},
                    {title: 'CHAMBER ', val: 'EQP_CHAMBER_CODE_TBL'},
                    {title: ' ', val: 'ENV_VIOLATION_CODE_TBL'},
                    {title: ' ', val: 'ENV_ACCIDENT_CODE_TBL'},
                    {title: ' ', val: 'ENV_EXPOSE_CODE_TBL'},
                    {title: ' ', val: 'ENV_ACCIDENT_TYPE_CODE_TBL'},
                    {title: ' ', val: 'ENV_IRRATION_CODE_TBL'},
                    {title: '  ', val: 'ENV_IRRATION_WITH_ACCIDENT_CODE_TBL'},
                    {title: '', val: 'EQP_ALARM_CODE_TBL'}
                ],
                pageSize: pageSize,
                currPage : 1,
                totSize: null
            };
        },
        methods: {
            openArea: function() {

            },
            getList: function() {
                var url = this.getUrl();
                url += "?mode=list";

                this.$store.dispatch('getApiCall', url).then(data => {
                    this.codeList = data;

                    this.mappingField();
                });

            },
            getTotal: function() {
                var url = this.getUrl();
                url += "?mode=total";

                this.$store.dispatch('getApiCall', url).then(data => {
                    this.totSize = data[0].TOTAL_SIZE;
                });
            },
            getUrl: function() {
                var url = '';
                switch(this.workTable) {
                    case 'WORK_PLACE_CODE_TBL' :
                        url = '/place-code'
                        break;
                    case 'WORK_PART_CODE_TBL' :
                        url = '/part-code'
                        break;
                    case 'WORK_COMPLEX_CODE_TBL' :
                        url = '/complex-code'
                        break;
                    case 'WORK_LINE_CODE_TBL' :
                        url = '/line-code'
                        break;
                    case 'WORK_SITE_CODE_TBL' :
                        url = '/site-code'
                        break;
                    case 'WORK_FLOOR_CODE_TBL' :
                        url = '/floor-code'
                        break;
                    case 'WORK_BAY_CODE_TBL' :
                        url = '/bay-code'
                        break;
                    case 'USER_POSITION_CODE_TBL' :
                        url = '/position-code'
                        break;
                    case 'USER_DUTY_CODE_TBL' :
                        url = '/duty-code'
                        break;
                    case 'EQP_LINE_EMS_CODE_TBL' :
                        url = '/ems-code'
                        break;
                    case 'EQP_PROCESS_CODE_TBL' :
                        url = '/process-code'
                        break;
                    case 'EQP_SUB_PROC_CODE_TBL' :
                        url = '/sub-process-code'
                        break;
                    case 'EQP_SCR_CODE_TBL' :
                        url = '/scr-code'
                        break;
                    case 'EQP_SCR_MODEL_CODE_TBL' :
                        url = '/scr-model-code'
                        break;
                    case 'EQP_TYPE1_CODE_TBL' :
                        url = '/eqp-type1-code'
                        break;
                    case 'EQP_TYPE2_CODE_TBL' :
                        url = '/eqp-type2-code'
                        break;
                    case 'EQP_GAS_INFO_TBL' :
                        url = '/gas-code'
                        break;
                    case 'EQP_EXHAUST_CODE_TBL' :
                        url = '/exhaust-code'
                        break;
                    case 'EQP_WASTE_INFO_TBL' :
                        url = '/waste-code'
                        break;
                    case 'EQP_MAIN_MAKER_CODE_TBL' :
                        url = '/main-maker-code'
                        break;
                    case 'EQP_MAIN_MODEL_CODE_TBL' :
                        url = '/main-model-code'
                        break;
                    case 'EQP_SCR_MAKER_CODE_TBL' :
                        url = '/scr-maker-code'
                        break;
                    case 'EQP_MGR_MAKER_CODE_TBL' :
                        url = '/mgr-maker-code'
                        break;
                    case 'EQP_CHAMBER_CODE_TBL' :
                        url = '/chamber-code'
                        break;
                    case 'ENV_VIOLATION_CODE_TBL' :
                        url = '/violation-code'
                        break;
                    case 'ENV_ACCIDENT_CODE_TBL' :
                        url = '/accident-code'
                        break;
                    case 'ENV_EXPOSE_CODE_TBL' :
                        url = '/expose-code'
                        break;
                    case 'ENV_ACCIDENT_TYPE_CODE_TBL' :
                        url = '/accident-type-code'
                        break;
                    case 'ENV_IRRATION_CODE_TBL' :
                        url = '/irration-code'
                        break;
                    case 'ENV_IRRATION_WITH_ACCIDENT_CODE_TBL' :
                        url = '/irration-accident-code'
                        break;
                    case 'EQP_ALARM_CODE_TBL' :
                        url = '/alram-code'
                        break;
                    default:
                        url = '';
                        break;
                }

                if (url.length > 0) {
                    url = '/api/common' + url;
                }

                return url;
            },
            mappingField: function() {
                if (this.codeList.length < 1) {
                    console.log('[mappingField]not code data!!');
                    return false;
                }

                var keySet = Object.keys(this.codeList[0]);
                var tempList = [];
                this.codeList.forEach(elem => {
                    var tempObj = new Object();
                    keySet.forEach(key => {
                        if (key.indexOf('_CODE') > -1) {
                            tempObj.ITEM_CODE = elem[key];
                        } else if (key.indexOf('_NAME') > -1) {
                            tempObj.ITEM_NAME = elem[key];
                        }
                    });

                    tempList.push(tempObj);
                });

                this.viewList = tempList;   
            }
        }
    }
</script>