<template>
    <div>
        <b> </b>
        <div class="content_in_start"></div>
        <div class="work_btn" @click="openArea(0)"> </div>
        <table>
            <thead>
                <tr>
                    <th style="width:calc(12%)"></th>
                    <th style="width:calc(12%)"></th>
                    <th style="width:calc(12%)"></th>
                    <th style="width:calc(12%)"></th>
                    <th style="width:calc(12%)"></th>
                    <th></th>
                    <th style="width:calc(12%)"></th>
                    <th style="width:calc(12%)"></th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, idx) in teamCodeList" v-bind:key="idx">
                    <td>{{item.WORK_PLACE_CODE}}</td>
                    <td>{{item.WORK_PART_CODE}}</td>
                    <td>{{item.WORK_COMPLEX_CODE}}</td>
                    <td>{{item.WORK_LINE_CODE}}</td>
                    <td>{{item.WORK_TEAM_CODE}}</td>
                    <td>{{item.WORK_TEAM_NAME}}</td>
                    <td>{{item.TEAM_EQP_TYPE}}</td>
                    <td>
                        <div class="tdbtn" style="width:40px;" @click="openArea(1, idx)"></div>
                        <div class="tdbtn" style="width:40px;margin-left:7px" @click="delCheck(idx)"></div>
                    </td>
                </tr>
            </tbody>
        </table>
        <div v-if="openInput">
            <br />
            <table>
                <tr>
                    <td style="width:calc(12%)">
                        <dataSelectBox v-model="objData.placeCd" :items="list.placeCode" :input_id="'placeCode'" @input="val => {objData.placeCd = val}" :viewText="' '" /> 
                    </td>
                    <td style="width:calc(12%)">
                        <dataSelectBox v-model="objData.partCd" :items="list.partCode" :input_id="'partCode'" @input="val => {objData.partCd = val}" :viewText="' '" 
                            :setVal="objData.partCd" :disabled="list.partCode.length < 1" /> 
                    </td>
                    <td style="width:calc(12%)">
                        <dataSelectBox v-model="objData.complexCd" :items="list.complexCode" :input_id="'complexCode'" @input="val => {objData.complexCd = val}" :viewText="' '" 
                            :disabled="list.complexCode.length < 1" :setVal="objData.complexCd" />
                    </td>
                    <td style="width:calc(12%)">
                        <dataSelectBox v-model="objData.lineCd" :items="list.lineCode" :input_id="'lineCode'" @input="val => {objData.lineCd = val}" :viewText="' '" 
                            :disabled="list.lineCode.length < 1" :setVal="objData.lineCd" /> 
                    </td>
                    <td style="width:calc(12%)"><input type="text" v-model="objData.teamCd" placeholder="" :readonly="this.objData.userFrom != 0"></td>
                    <td><input type="text" v-model="objData.teamNm" placeholder=""></td>
                    <td style="width:calc(12%)">
                        <dataSelectBox v-model="objData.eqpType" :items="list.eqpType" :input_id="'lineCode'" @input="val => {objData.eqpType = val}" :viewText="' '" 
                            :disabled="list.eqpType.length < 1" :setVal="objData.eqpType" /> 
                    </td>
                    <td style="width:calc(12%)">
                        <div class="tdbtn" style="width:40px;" @click="checkValid"></div>
                        <div class="tdbtn" style="width:40px;margin-left:7px" @click="closeArea"></div>
                    </td>
                </tr>
            </table>
        </div>
        <msgbox :title="boxObj.title" :message="boxObj.msg" :boxType="boxObj.bType" v-if="showMsgBox != null && showMsgBox" @close="showMessagebox" @callback="procRegister" />
    </div>
</template>
<script>
    import dataSelectBox from '@/components/dataSelectBox';
    import msgbox from  '@/components/messageBox';
    export default {
        components: {
            dataSelectBox, msgbox
        },
        data() {
            return {
                list: {
                    placeCode: [],
                    partCode: [],
                    complexCode: [],
                    lineCode: [],
                    eqpType: [
                        {val:0, title:''},
                        {val:1, title:''}
                    ]
                },
                objData: {
                    userFrom: 0,
                    placeCd: null,
                    partCd: null,
                    complexCd: null,
                    lineCd: null,
                    teamCd: null,
                    teamNm: null,
                    eqpType: null,
                    userId: this.$store.getters.getId
                },
                boxObj: {
                    title: null,
                    msg: null,
                    bType: null
                },
                teamCodeList: [],
                openInput: false,
                isShow: false
            };
        },
        created() {
            this.$emit('setMenu', 81); //  
            this.getList();
        },
        computed: {
            showMsgBox: function() {
                return this.isShow;
            }
        },
        methods: {
            getList: function() {
                var url = "/api/common/team-code";
                this.$store.dispatch('getApiCall', url).then(data => {
                    this.teamCodeList = data;
                });
            },
            openArea: async function(mode, idx) {
                this.closeArea();
                
                this.objData.userFrom = mode;
                try {
                    this.$store.commit('SET_LOADING', true); //  

                    if (this.list.placeCode.length < 1) {
                        await this.commonUtil.getPlaceList().then(data => {
                            this.list.placeCode = data;
                        });
                    }

                    if (this.list.partCode.length < 1) {
                        await this.commonUtil.getPartList().then(data => {
                            this.list.partCode = data;
                        });
                    }

                    if (this.list.complexCode.length < 1) {
                        await this.commonUtil.getComplexList().then(data => {
                            this.list.complexCode = data;
                        });
                    }
                    if (this.list.lineCode.length < 1) {
                        await this.commonUtil.getLineList().then(data => {
                            this.list.lineCode = data;
                        });
                    }

                    if (idx != null) {
                        this.objData.placeCd = this.teamCodeList[idx].WORK_PLACE_CODE;
                        this.objData.partCd = this.teamCodeList[idx].WORK_PART_CODE;
                        this.objData.complexCd = this.teamCodeList[idx].WORK_COMPLEX_CODE;
                        this.objData.lineCd = this.teamCodeList[idx].WORK_LINE_CODE;
                        this.objData.teamCd = this.teamCodeList[idx].WORK_TEAM_CODE;
                        this.objData.teamNm = this.teamCodeList[idx].WORK_TEAM_NAME;
                        this.objData.eqpType = this.teamCodeList[idx].TEAM_EQP_TYPE;
                    }
                    this.openInput = true;
                } catch(err) {
                    console.log(err.message);
                } finally {
                    this.$store.commit('SET_LOADING', false); //  
                }
            },
            closeArea: function() {
                this.objData = {
                    userFrom: 0,
                    placeCd: null,
                    partCd: null,
                    complexCd: null,
                    lineCd: null,
                    teamCd: null,
                    teamNm: null,
                    eqpType: null,
                    userId: this.$store.getters.getId
                }

                this.openInput = false;
            },
            checkValid: function() {
                if (this.objData.teamCd == null || this.objData.teamCd.length < 1) {
                    this.showMessagebox(true, 'alert', ' ', '  .');
                    return false;
                }
                if (this.objData.teamNm == null || this.objData.teamNm.length < 1) {
                    this.showMessagebox(true, 'alert', ' ', '  .');
                    return false;
                }

                if (this.objData.userFrom == 0) {
                    this.showMessagebox(true, 'confirm', '', ' ?');
                } else if (this.objData.userFrom == 1) {
                    this.showMessagebox(true, 'confirm', '', '  ?');
                } else {
                    return false;
                }
            },
            delCheck: function(idx) {
                this.objData.userFrom = 2;
                this.objData.placeCd = this.teamCodeList[idx].WORK_PLACE_CODE;
                this.objData.partCd = this.teamCodeList[idx].WORK_PART_CODE;
                this.objData.complexCd = this.teamCodeList[idx].WORK_COMPLEX_CODE;
                this.objData.lineCd = this.teamCodeList[idx].WORK_LINE_CODE;
                this.objData.teamCd = this.teamCodeList[idx].WORK_TEAM_CODE;
                this.objData.teamNm = this.teamCodeList[idx].WORK_TEAM_NAME;
                this.objData.eqpType = this.teamCodeList[idx].TEAM_EQP_TYPE;

                this.showMessagebox(true, 'confirm', '', this.objData.teamCd + ' ?');
            },
            procRegister: function() {
                this.objData.url = "/api/proc/put-team-code";
                this.$store.dispatch('postApiCall', this.objData).then(data => {
                    if (!data.isSuccess) {
						this.showMessagebox(true, 'alert', ' ', data.message);
						return;
					} else {
                        if (this.objData.userFrom == 0) {
                            this.showMessagebox(true, 'alert', ' ', '  .');
                        } else if (this.objData.userFrom == 1) {
                            this.showMessagebox(true, 'alert', ' ', '  .');
                        }
                        this.closeArea();
                        this.getList();
						return;
					}
                })
            },
            showMessagebox: function(val, mode, title, msg) {                
				this.boxObj.bType = mode;
				this.boxObj.title = title;
				this.boxObj.msg = msg;
				this.isShow = val;
            }
        }
    }
</script>

<style type="less">
    .content_in table td select {
        width:calc(100% - 10px);padding-left:15px
    }
    .content_in table td select:disabled {
        width:calc(100% - 10px);padding-left:15px
    }
</style>